import './App.css';

import { CartesianGrid, Legend, Line, LineChart, Tooltip, XAxis, YAxis } from 'recharts';
import React, { Component } from 'react';

import Papa from 'papaparse';
import logo from './logo.svg';

const URL = 'https://raw.githubusercontent.com/mincongzhang/HistoricalForexAnalysis/master/TODO/EURUSD_20160101.csv';



class App extends Component {

  state = {
    data: null
  }

  componentDidMount() {
    fetch(URL).then(res => {
      res.text().then(text => {
        // console.log('text', text);
        const csv = Papa.parse(text);
        console.log('csv:', csv);
        const data = csv.data.map(r => {
          return { rate: Number(r[1]), name: r[0] };
        });
        console.log('refined data:', data);
        this.setState({ data: data });
      })
    })
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">EURUSD_20160101</h1>
        </header>
      

        <div>
          {this.state.data ? this.renderChart() : 'loading...'}
        </div>
      </div>
    );
  }

  renderChart() {
    return (
      <LineChart width={1024} height={768} data={this.state.data}>
        <Line type="basis" dataKey="rate" stroke="#8884d8" strokeWidth={1} activeDot={{r: 8}} />
        <XAxis dataKey="name"/>
        <YAxis dataKey="rate" domain={['dataMin - 0.001', 'dataMax + 0.002']} />
        <Tooltip />
        <CartesianGrid strokeDasharray="5.5 5.5" />
        <Legend />
      </LineChart>
    );
  }

  
}

export default App;



